.button {
  color: #fff;
  width: 145px;
  background-color: #f3848c;
  border: 1px solid #f3848c;
  border-radius: 25px;
  padding: 9px 35px;
  font-size: 18px;
  line-height: 1.2;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), filter .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 10px 15px 25px #e8b7b773;
}

.button:hover, .button:focus {
  color: #f3848c;
  filter: drop-shadow(10px 15px 25px #e8b7b773);
  background-color: #fff;
}

.button:active {
  filter: drop-shadow(10px 15px 25px #e8b7b773);
  color: #fff;
  background-color: #f3848c;
  border: 1px solid #fff;
  box-shadow: inset 0 4px 4px #6b424240;
}

.socials {
  width: 80px;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.socials__item {
  border: 0;
  padding: 0;
  display: inline-flex;
}

.socials__link {
  fill: #3c322d;
  line-height: 1;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.socials__link:hover, .socials__link:focus {
  fill: #f3848c;
}

.footer__icon {
  cursor: pointer;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.footer__icon:hover, .footer__icon:focus {
  fill: #f3848c;
}

.button-scroll {
  width: 15px;
  height: 15px;
  opacity: 0;
  cursor: pointer;
  background-color: #0000;
  background-image: url("double-arrow-top-icon.c970aeea.svg");
  background-repeat: no-repeat;
  border: none;
  border-radius: 50%;
  transition: opacity .4s linear;
  animation-name: play_top;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  position: fixed;
  bottom: 30px;
  right: 20px;
  box-shadow: 0 0 0 3px #00000080;
}

@media screen and (min-width: 768px) {
  .button-scroll {
    width: 20px;
    height: 20px;
    bottom: 40px;
    right: 30px;
  }
}

@media screen and (min-width: 1280px) {
