    width: 25px;
    height: 25px;
    bottom: 50px;
    right: 40px;
  }
}

.btn-style {
  opacity: 1;
}

@keyframes play_top {
  0% {
    -webkit-box-shadow: 0 0 #e92b9441;
    -moz-box-shadow: 0 0 #f11b9b72;
    box-shadow: 0 0 #ec41aaba;
  }

  to {
    -webkit-box-shadow: 0 0 0 20px #f182b200;
    -moz-box-shadow: 0 0 0 20px #ea80bc00;
    box-shadow: 0 0 0 20px #f053af00;
